import { defineComponent, ref } from 'vue';
import XtForm from '@/base-ui/form';
export default defineComponent({
  name: 'PageSearch',
  props: {
    searchFormConf: {
      type: Object,
      reuqired: true
    }
  },
  components: {
    XtForm
  },
  emits: ['resetBtnClick', 'submitBtnClick'],

  setup(props, {
    emit
  }) {
    //formData由config动态生成
    const formItems = props.searchFormConf?.formItems ?? [];
    const formOriginData = {};

    for (const item of formItems) {
      formOriginData[item.field] = '';
    }

    const formData = ref(formOriginData); //    点击重置

    const handleReset = () => {
      emit('resetBtnClick', formData.value);

      for (const key in formOriginData) {
        formData.value[`${key}`] = formOriginData[key];
      }
    }; //点击搜索


    const handleSearch = () => {
      console.log('搜索');
      emit('submitBtnClick', formData.value);
    };

    return {
      formData,
      handleReset,
      handleSearch
    };
  }

});